import "~/extension/HC2DDom";
import "~/extension/Hc2DGui";
import "~/extension/HC2DTexture";

import {
  AppConfig,
  Application,
  AppStore,
  CameraType,
  IVariableOption,
} from "@hc/engine";
import * as HC from "@hc/engine";
import React, { useEffect, useRef, useState } from "react";
import { createRoot } from "react-dom/client";

import { DirSourceType, FileApi, MarketApi, ProjectApi } from "./apis";
import { getAppDetail, getAppDetailByUid } from "./apis/appReq";
import { setAppIntance, setPrefix } from "./intance";
import { commonStore } from "./stores/commonStore";
import { getReq, myAxios, ResponseStatus } from "./utils/axios";
import {
  getFunction,
  getUniupComponentType,
  registerComponent,
} from "./utils/component";
import { loadOnlineFile } from "./utils/loadFile";
import { PreviewData } from "./apis/types";
import { Box, Button } from "@mui/material";
import { LinearProgressWithLabel2 } from "./components/common/circularProgressWithLabel";
import { sleep } from "./utils/utils";
import checkVerUpdate from "./utils/checkVerUpdate";
import { SnackbarProvider } from "notistack";
import Toaster from "./components/common/toaster";
window["IGNORE_INVAILID_TOKEN"] = true;

interface PreviewProps {}

checkVerUpdate();

AppConfig.assetURL = ASSET_URL;
AppConfig.Texture_URL = "https://am-img.gkiiot.com/editor/";
AppConfig.SkyBox_URL = "https://am-img.gkiiot.com/editor/";

let app: Application;
let previewChannel: BroadcastChannel;
const Preview: React.FC<PreviewProps> = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [url2d, setUrl2d] = useState("");
  const [progress, setProgress] = useState({
    start: false,
    value: 0,
    label: "",
  });
  const [roam, setRoam] = useState({ disable: true, open: false });

  const loadVariables = async (): Promise<IVariableOption[]> => {
    const url = new URLSearchParams(location.search);
    const timeStap = url.get("time");

    if (timeStap) {
      const json = localStorage.getItem("variables");
      if (json) return JSON.parse(json);
    }

    const projectId = url.get("projectId");

    localStorage.removeItem("variables");

    if (!projectId) return;

    const pdetail = await getReq(ProjectApi.detail + projectId);
    if (pdetail.code === ResponseStatus.Ok && pdetail.data?.info) {
      const variable = pdetail.data.info.variable;
      if (variable) {
        return JSON.parse(variable);
      }
    }

    return null;
  };

  const loadFile = async (): Promise<string | null> => {
    const url = new URLSearchParams(location.search);
    const id = url.get("id");

    const actual = url.get("actual");
    if (actual) return;

    if (id) {
      //加载全局变量
      const vars = await loadVariables();
      if (vars) {
        AppStore.GlobalVariables = vars;
      }
      const fileData = await getAppDetail(Number(id));
      return fileData;
    }

    const uid = url.get("uid");

    if (uid) {
      const fileData = await getAppDetailByUid(uid);
      return fileData;
    }

    return null;
  };
  const loadModel = async (): Promise<boolean> => {
    const url = new URLSearchParams(location.search);
    const id = url.get("model");

    if (id) {
      const result = await getReq(FileApi.detail + "/" + id);
      if (result.code === ResponseStatus.Ok) {
        app.Viewer.sceneControl.createDefaultEnv();
        const info = result.data.info;
        const url = info?.uuid + "/" + info.file_key;
        const en = app.create({ type: "ImportEntity", url });
        en.on("load", () => {
          setTimeout(() => {
            app.zoomAll();
          }, 100);
        });
        return true;
      }
    }
    return false;
  };

  const renderComponent = (json: string, id: number) => {
    const jsonData = JSON.parse(json);
    const d = jsonData.data;
    registerComponent({ ...d, id });
    // const url = info?.uuid + "/" + info.file_key;
    const en = app.create({ type: getUniupComponentType(id) });
    en.on("load", () => {
      setTimeout(() => {
        app.zoomAll();
      }, 100);
    });
  };

  const loadComponent = async (): Promise<boolean> => {
    const url = new URLSearchParams(location.search);
    const id = url.get("component");

    if (id) {
      const result = await getReq(FileApi.detail + "/" + id);
      if (result.code === ResponseStatus.Ok) {
        app.Viewer.sceneControl.createDefaultEnv();
        const info = result.data.info;
        const json = info.json_data;
        renderComponent(json, info.id);
        return true;
      }
    }
    return false;
  };

  const loadShop = async () => {
    const url = new URLSearchParams(location.search);
    const shop = url.get("shop");
    if (shop) {
      const res = await getReq<PreviewData>(MarketApi.preview + shop);
      if (res.code === ResponseStatus.Ok) {
        switch (res.data.source_type) {
          case DirSourceType.Files:
          case "project":
          case "app": {
            if (!res.data.json_data) return false;
            if (res.data.variable) {
              AppStore.GlobalVariables = JSON.parse(res.data.variable);
            }
            const data = JSON.parse(res.data.json_data);
            loadOnlineFile(data, myAxios, app);
            break;
          }
          case DirSourceType.Component: {
            app.Viewer.sceneControl.createDefaultEnv();
            renderComponent(res.data.json_data, Date.now());
            break;
          }
          case DirSourceType.Model: {
            app.Viewer.sceneControl.createDefaultEnv();
            const url = res.data.data;
            const en = app.create({ type: "ImportEntity", url });
            en.on("load", () => {
              setTimeout(() => {
                app.zoomAll();
              }, 100);
            });
            break;
          }
        }
      }
      return true;
    }
    return false;
  };

  const toggleRoam = () => {
    setRoam({ disable: false, open: !roam.open });
    app.Viewer.cameraManager.Type = !roam.open
      ? CameraType.FreeCamera
      : CameraType.ArcRotate;
  };

  useEffect(() => {
    previewChannel = new BroadcastChannel("preview_channel");
    previewChannel.addEventListener("message", (evt) => {
      if (evt?.data?.type === "data") {
        commonStore.loadUsedComponent(evt.data.components).then((res) => {
          app.loadFile(evt.data.data);
        });
      }
    });
    window.addEventListener("message", function (e) {
      //TODO:
      // console.log("2d数据：" + e.data);
    });
  }, []);
  useEffect(() => {
    (async () => {
      await setPrefix();
      if (canvasRef.current) {
        const canvas = canvasRef.current;
        canvas.width = canvas.parentElement.clientWidth;
        canvas.height = canvas.parentElement.clientHeight;

        window["app"] = app = new Application({
          canvas,
          editor: false,
          cameraRadius: 100,
        });
        setAppIntance(app);

        app.on("loadingBackground", (event) => {
          setProgress((progress) => {
            let value = progress.value + 5;
            if (value > 90) value = progress.value;
            return {
              ...progress,
              value: value,
              label: `正在加载天空图:`,
            };
          });
        });
        app.on("loadBackground", (event) => {
          setProgress((progress) => {
            return {
              start: false,
              value: 0,
              label: `天空图加载完成`,
            };
          });
        });

        app.on("loading", async (event) => {
          await sleep(100);
          setProgress((progress) => {
            return {
              start: true,
              value: event.percentage,
              label:
                event.percentage === 100
                  ? `整理资源`
                  : `正在加载${event.target?.Name}`,
            };
          });
        });
        app.on("load", async (event) => {
          await sleep(100);

          setProgress((progress) => {
            return {
              start: false,
              value: 100,
              label: `加载${event.target?.Name}完成`,
            };
          });
        });

        const fileData = await loadFile();
        if (fileData) {
          const data = JSON.parse(fileData);
          await commonStore.loadUsedComponent(data.components);
          setUrl2d(data.url2d);
          app.loadFile(data.data);
          setRoam({
            disable: false,
            open: app.Viewer.cameraManager.Type === CameraType.FreeCamera,
          });
          const scripts = data.scripts ?? [];
          for (const script of scripts) {
            const f = getFunction(script.code)();
            f(app, HC);
          }
          return;
        }

        let status = await loadModel();

        if (status) return;

        status = await loadComponent();
        if (status) return;

        status = await loadShop();
        if (status) return;

        if (previewChannel) {
          previewChannel.postMessage("load");
        }
      }
    })();
  }, []);

  return (
    <SnackbarProvider maxSnack={3} autoHideDuration={1000}>
      <Box
        style={{
          width: "100%",
          height: "100%",
          position: "relative",
        }}
      >
        <iframe
          src={url2d}
          style={{
            width: "100%",
            height: "100%",
            pointerEvents: "none",
            backgroundColor: "transparent",
            border: "0",
            position: "fixed",
          }}
        ></iframe>
        <canvas style={{ width: "100%", height: "100%" }} ref={canvasRef}>
          预览页面
        </canvas>
        {/* <div style={{ position: "fixed", bottom: "5%", left: "5%" }}>
        <h5 style={{ marginBottom: 5 }}>帮助说明</h5>
        <div style={{ marginBottom: 5 }}>1.按住鼠标滚轮进行旋转</div>
        <div style={{ marginBottom: 5 }}>2.滚动滚轮进行缩放</div>
        <div style={{ marginBottom: 5 }}>3.Alt+鼠标中键移动视角</div>
        {!roam.disable && (
          <div style={{ marginBottom: 5 }}>
            4.漫游模式下，WASD控制移动方向，鼠标控制视角
          </div>
        )}
      </div> */}
        {/* {!roam.disable && (
        <Button
          variant="contained"
          sx={{ position: "fixed", right: "5%", top: "5%" }}
          size="small"
          onClick={toggleRoam}
        >
          {roam.open ? "关闭漫游" : "开启漫游"}
        </Button>
      )} */}
        {progress.start && (
          <Box
            style={{
              position: "absolute",
              left: 0,
              top: 0,
              width: "100%",
              height: "100%",
              backgroundColor: "rgba(0,0,0,0.8)",
              color: "#FCFCFC",
            }}
          >
            <Box
              style={{
                position: "absolute",
                top: "50%",
                left: "50%",
                width: "50%",
                transform: "translate(-50%, -50%)",
              }}
            >
              <LinearProgressWithLabel2
                sx={{
                  height: 15,
                  borderRadius: 5,
                }}
                value={progress.value}
                label={progress.label}
              />
            </Box>
          </Box>
        )}
      </Box>
      <Toaster />
    </SnackbarProvider>
  );
};

const root = createRoot(document.getElementById("app"));

root.render(<Preview />);
