import type { Application } from "@hc/engine";
import { Axios } from "axios";
import { FileApi } from "~/apis";
import { getReq, ResponseStatus } from "./axios";
import { getUniupComponentType, registerComponent } from "./component";
import { updateQueryStringParameter } from "./url";

export async function loadOnlineFile(
  data: { components: number[]; data },
  intance: Axios,
  app
) {
  const components = data.components ?? [];
  for (const id of components) {
    const res = await intance.get(FileApi.detail2 + id);

    if (res["status"] === 200 && res.data.code === 0) {
      const json = res.data.data.json_data;
      const data = JSON.parse(json);
      registerComponent({ ...data.data, id });
    }
  }
  app.loadFile(data.data);
}

export async function loadOnlineComponent(
  id: number,
  app: Application,
  option: any = {}
) {
  const result = await getReq(FileApi.detail + "/" + id);
  if (result.code === ResponseStatus.Ok) {
    const info = result.data.info;
    const json = info.json_data;
    const jsonData = JSON.parse(json);
    const d = jsonData.data;
    registerComponent({ ...d, id });
    const en = app.create({ type: getUniupComponentType(id), ...option });
    app.Editor.selectControl.SelectEntitys = [en];
    return true;
  }
  return false;
}

export async function loadOnlineModel(
  id: number,
  app: Application,
  option: any = {}
) {
  const result = await getReq(FileApi.detail + "/" + id);
  if (result.code === ResponseStatus.Ok) {
    const info = result.data.info;
    const url = info?.uuid + "/" + info.file_key;
    const en = app.create({ type: "ImportEntity", url, ...option });
    app.Editor.selectControl.SelectEntitys = [en];
    return true;
  }
  return false;
}

export function updateUrl(id: number) {
  const newUrl = updateQueryStringParameter(location.href, "id", id.toString());
  history.replaceState(
    {
      path: newUrl,
    },
    "",
    newUrl
  );
}
