import { IAppReturnData } from "./types";
import { AppApi, DirSourceType } from "~/apis";
import {
  deleteReq,
  getReq,
  postReq,
  putReq,
  ResponseStatus,
} from "~/utils/axios";

export async function uploadApp({
  name,
  pic_key,
  jsonData,
  resources,
  projectId,
}: {
  name: string;
  pic_key: number | string;
  sourceType: DirSourceType;
  jsonData: string;
  resources: string;
  projectId: number;
}) {
  const result = await postReq(AppApi.upload, {
    name,
    pic_key,
    json_data: jsonData,
    resources,
    status: 1,
    project_id: projectId,
    project_type: "3d",
  });

  return result;
}

export async function getAppList(
  projectId: number,
  name?: string
): Promise<null | IAppReturnData[]> {
  const result = await getReq<IAppReturnData[]>(AppApi.list, {
    params: { projectId, name },
  });

  if (result.code === ResponseStatus.Ok) {
    return result.data;
  }
  return null;
}

export async function updateApp(data: {
  id: number;
  name?: string;
  jsonData?: string;
  resources?: string;
  project_id: number;
}) {
  const result = await putReq(AppApi.update, { ...data });

  return result;
}

export async function uploadAppPreview(
  id: number,
  callback: () => Promise<string>
) {
  const base64 = await callback();
  const result = await postReq(AppApi.pic, { id, data: base64 });
  return result.code !== ResponseStatus.Ok;
}

export async function getAppDetail(id: number) {
  const result = await getReq(AppApi.detail + "/" + id);
  if (result.code === ResponseStatus.Ok) {
    return result.data?.info?.json_data;
  }
  return null;
}
export async function getAppDetailByUid(id: string) {
  const result = await getReq(AppApi.detail2 + "/" + id);
  if (result.code === ResponseStatus.Ok) {
    return result.data?.info?.json_data;
  }
  return null;
}
export async function getAppDetailInfo(id: number) {
  const result = await getReq(AppApi.detail + "/" + id);
  if (result.code === ResponseStatus.Ok) {
    return result.data?.info;
  }
  return null;
}

export async function deleteApp(id: number) {
  const result = await deleteReq(AppApi.delete, { data: { id } });

  return result.code === ResponseStatus.Ok;
}
