import { IAppReturnData, IFileReturnData } from "~/apis/types";

export const IMG_CDN = "https://am-img.gkiiot.com/editor/static/images/";

export function updateQueryStringParameter(
  uri: string,
  key: string,
  value: string
) {
  if (value === undefined) {
    return uri;
  }
  const re = new RegExp("([?&])" + key + "=.*?(&|$)", "i");
  const separator = uri.indexOf("?") !== -1 ? "&" : "?";
  if (uri.match(re)) {
    return uri.replace(re, "$1" + key + "=" + value + "$2");
  } else {
    return uri + separator + key + "=" + value;
  }
}

export function getFileUrl(
  data: IFileReturnData | IAppReturnData,
  isIcon = false,
  hasTimeStap = true
) {
  let endUrl = isIcon ? data.pic_key : data.file_key;

  if (data.uuid) {
    endUrl = data.uuid + "/" + endUrl;
  }
  if (isIcon && hasTimeStap)
    endUrl = updateQueryStringParameter(endUrl, "t", Date.now().toString());

  return endUrl;
}

export function updateLocationUrl(key: string, value: string) {
  const newUrl = updateQueryStringParameter(location.href, key, value);
  history.replaceState(
    {
      path: newUrl,
    },
    "",
    newUrl
  );
}

export function deleteQueryKey(url: string, key: string, isUpdate = false) {
  const urlObject = new URL(url);
  urlObject.searchParams.delete(key);
  if (isUpdate) {
    history.replaceState(
      {
        path: urlObject.href,
      },
      "",
      urlObject.href
    );
  }
  return urlObject.href;
}
