import { OptionsObject, useSnackbar } from "notistack";
import React, { useEffect } from "react";
import { message, ToasterKeys } from "./../../utils/message";

const anchorOrigin = {
  vertical: "top" as "top",
  horizontal: "center" as "center",
};

const Toaster: React.FC = (props) => {
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();
  useEffect(() => {
    // 'default' | 'error' | 'success' | 'warning' | 'info';
    message.info = (msg: string, option: OptionsObject = {}) => {
      ToasterKeys.add(option.key);
      enqueueSnackbar(msg, {
        variant: "info",
        anchorOrigin: anchorOrigin,
        ...option,
      });
    };
    message.warn = (msg: string, option: OptionsObject = {}) => {
      ToasterKeys.add(option.key);
      enqueueSnackbar(msg, {
        variant: "warning",
        anchorOrigin: anchorOrigin,
        ...option,
      });
    };
    message.success = (msg: string, option: OptionsObject = {}) => {
      ToasterKeys.add(option.key);
      enqueueSnackbar(msg, {
        variant: "success",
        anchorOrigin: anchorOrigin,
        ...option,
      });
    };
    message.error = (msg: string, option: OptionsObject = {}) => {
      ToasterKeys.add(option.key);
      enqueueSnackbar(msg, {
        variant: "error",
        anchorOrigin: anchorOrigin,
        ...option,
      });
    };
    message.close = (key) => {
      ToasterKeys.delete(key);
      closeSnackbar(key);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return <></>;
};
export default Toaster;
